import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
    container: {
        margin: 'auto',
        display: 'flex',
        justifyContent: 'center',
        '@media(max-width: 600px)': {
            display: 'block',
            margin: '20px auto 50px auto'
        }
    },
    block: {
        width: '120px',
        height: '10%',
        border: '1px solid #dedddd',
        padding: '4px',
        margin: '5px',
        fontSize: '.9rem',
        flexFlow: 'row',
        '@media(max-width: 600px)': {
            width: '120px',
            margin: '5px auto',
            height: 'auto'
        }
    },
    label: {
        color: '#00A170',
        fontWeight: '500'
    }
})

const Countdown = (props) => {

    const classes = useStyles();

    const yearDifference = props.endDate.getFullYear() - props.startDate.getFullYear();
    const monthDifference = (props.endDate.getMonth() + 1) - (props.startDate.getMonth() + 1);
    const dayDiffrence = props.endDate.getDate() - props.startDate.getDate();
    const hourDiffence = props.endDate.getHours() - props.startDate.getHours() + (dayDiffrence * 24);
    const minutesDifference = props.endDate.getMinutes() - props.startDate.getMinutes();
    const secondDifference = props.endDate.getSeconds() - props.startDate.getSeconds();

    const block = (value, label) => (
        <div className={classes.block}>
            <div>{value}</div>
            <div className={classes.label}>{label}</div>
        </div>
    )

    return (
        <div className={classes.container}>
            {block(yearDifference, 'A침os')}
            {block(monthDifference, 'Meses')}
            {block(dayDiffrence, 'D칤as')}
            {block(hourDiffence, 'Horas')}
            {block(minutesDifference, 'Minutos')}
            {block(secondDifference, 'Segundos')}
        </div>
    )
}

export default Countdown;